import React, { Component } from 'react';
import Popup from 'reactjs-popup'


class Prompt extends Component {
    constructor(props) {
        super(props);

        this.state = { open: true }
        this.closeModal = this.closeModal.bind(this);
    }

    closeModal() {

        this.setState({ open: false });
    }

    render() {

        let renderOutput;

        //type is single or dialogprompt
        if (this.props.type === "single") {
            renderOutput =
                <div>
                    {this.props.title && <h2>{this.props.title}</h2>}
                    {this.props.image && <img src={this.props.image} />}
                    {this.props.text && <div className="prompt-text">{this.props.text}</div>}
                    <button onClick={this.closeModal}>{this.props.buttontext}</button>
                </div>
        }
        else {
            renderOutput = <div>No Output</div>
        }
        

        return (
            <Popup
                open={this.state.open}
                closeOnDocumentClick={false}
                onClose={this.props.promptComplete}
            >
                {renderOutput}
            </Popup>
        );
    }
}

Prompt.defaultProps = {
    type: "dialogprompt",
    buttontext : "Continue"
}

export default Prompt;
